import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import classNames from 'classnames';
import compose from '../../../compose';

const styles = theme => ({
    icon: {
        height: '20px',
        width: 'auto'
    }
});

function Icon(props) {
    const { classes } = props;
    return (
        <>            
            <svg
                xmlns="http://www.w3.org/2000/svg"
                width="100%"
                height="100%"
                fill="none"
                viewBox="0 0 32 32"
                className={classNames(classes.icon, props.className)} style={{height: `${props.size}px`}}
            >
                <path stroke="currentColor" strokeWidth="1.5" d="M19.5 23l-7-7 7-7"></path>
            </svg>
        </>
    )
}

export default compose(
    withStyles(styles)
)(Icon);