import React from 'react';
//import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import Grid from '@material-ui/core/Grid';
import compose from '../../compose';
import withUser from '../../consumers/withUser';
//import Flickity from "react-flickity-component";
//import Button from '../dobyComponents/form/Button';
import withDoby from '../../consumers/withDoby';
import withStore from '../../consumers/withStore';
import moment from "moment";
import config from "../../config";

import StopwatchIcon from '../dobyComponents/icons/Stopwatch';
import MountainIcon from '../dobyComponents/icons/Mountain';
import FlagIcon from '../dobyComponents/icons/Flag';
import { withLocalization } from '../shared/localization';
import SummitCard from '../dobyComponents/SummitCard'
import Button from '../dobyComponents/form/Button';
import ChallengeCard from '../dobyComponents/ChallengeCardV2'
import Flickity from "react-flickity-component";
import Faq from "../dobyComponents/Faq";
import RedTriangle from "../dobyComponents/icons/RedTriangle";
import FacebookIcon from "../dobyComponents/icons/Facebook";
import InstagramIcon from "../dobyComponents/icons/Instagram";

const getSrc = (img) => {
    return `/img/${config.locale}/${img}`;
};

const styles = theme => ({
    ...getSharedStyles(theme),
    introSection: {
        // minHeight: '80vh',
        // height: '80vh',
        width: '100%',
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
        position: 'relative',
        '&:after': {
            content: "''",
            display: 'block',
            width: '100%',
            height: '100%',
            position: 'absolute',
            top: 0,
            left: 0,
            background: "linear-gradient(180deg, rgba(0, 20, 31, 0.32) 0%, rgba(2, 16, 25, 0.55) 56.58%, rgba(3, 15, 23, 0.75) 100%), url(image.png), url(image.png);"
        }
    },
    introContent: {
        zIndex: 10,
        position: "relative",
    },
    difficultyDot: {
        width: '9px',
        height: '9px',
        borderRadius: '10px',
        marginRight: '5px',
        background: 'white',
        opacity: '0.4'
    },
    dotFilled: {
        opacity: '1',
    },
    challengeMap: {
        height: '40px',
        width: 'auto'
    },
    bigMapSection: {
        width: '100%',
        marginTop: '-75px',
        position: 'relative'
    },
    bigMap: {
        width: '95%',
        maxWidth: '95%'
    },
    acceptChallengeBtn: {
        marginTop: '-26px',
    },
    bigPhotoHolder: {
        width: "100%",
        height: "400px",
    },
    bigPhotoContent: {
        width: "400px",
    },

    challengeCarousel: {
        '& .flickity-viewport': {
            //padding: '7px'
        },
        '& .carousel-cell:first-child': {
            paddingLeft: '1.25rem'
        },
        '& .carousel-cell:last-child': {
            paddingRight: '0.5rem',
        }
    },
    challengeCard: {
        width: '300px'
    },
    findNewPeople: {
        width: "100%",
        height: "270px",
        background: "url('/img/people-mountain.png')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        position: "relative",
        "&:after": {
            content: "''",
            display: "block",
            position: "absolute",
            bottom: "0",
            left: "0",
            width: "100%",
            height: "100%",
            background: "rgba(0,0,0,0.3)",
            zIndex: 1,
        },
    },
    findNewPeopleContent: {
        position: "relative",
        zIndex: 2,
    },
});

const flickityOptions = {
    cellAlign: 'left',
    contain: true,
    prevNextButtons: false,
    pageDots: false
}

class ChallengeDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            challenge: null,
            coverPhoto: null,
            logo: null,
            map: null,
            bigMap: null,
            summits: []
        }
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchData = async () => {
        const { _id } = this.props.match.params;
        await this.props.doby.getChallenges();
        const { challenges } = this.props.doby;

        const challenge = challenges.data.find(item => item._id === _id)
        console.log(challenge)
        if (challenge) {
            this.setState({ challenge: challenge })

            const { imageId, logoId, mapId, mapFullTransparentId } = challenge;
            if (imageId) {
                const data = await this.props.store.getFile(imageId);
                let reader = new FileReader();
                reader.onloadend = () => this.setState({ coverPhoto: reader.result });
                reader.readAsDataURL(data);
            }
            if (logoId) {
                const data = await this.props.store.getFile(logoId);
                let reader = new FileReader();
                reader.onloadend = () => this.setState({ logo: reader.result });
                reader.readAsDataURL(data);
            }
            if (mapId) {
                const data = await this.props.store.getFile(mapId);
                let reader = new FileReader();
                reader.onloadend = () => this.setState({ map: reader.result });
                reader.readAsDataURL(data);
            }
            if (mapFullTransparentId) {
                const data = await this.props.store.getFile(mapFullTransparentId);
                let reader = new FileReader();
                reader.onloadend = () => this.setState({ bigMap: reader.result });
                reader.readAsDataURL(data);
            }

            const summitFilter = [
                {
                    field: "_id",
                    op: "in",
                    value: challenge.summitsRef.map((x) => x.id),
                },
            ];
            const summits = await this.props.doby.getSummits(summitFilter);
            this.setState((state) => ({
                summits: summits.data,
            }));
        }


        await this.props.doby.getPosts([{ field: "type.key", value: 10 }], {
            field: "title",
            dir: "asc",
        });

    }


    renderSummits = () => {
        //console.log(this.state.summits)
        const { classes } = this.props;
        return this.state.summits.map((item) => {
            return (
                <Grid item xs={12} sm={2} xl={1} key={item._id} className={classNames(classes.mb7)}>
                    <SummitCard
                        title={item.name}
                        time={item.time}
                        elevation={item.elevation}
                        thumbnailId={item.thumbnailId}
                    />
                </Grid>
            );
        });
    }

    renderChallenges = () => {
        const { challenges } = this.props.doby;
        const { classes } = this.props;
        return challenges.data.map(challenge => {
            //const endDate = new Date(challenge.end);
            return (
                <div className={"carousel-cell"} key={challenge._id}>
                    <ChallengeCard
                        title={challenge.name}
                        description={challenge.description}
                        start={challenge.start}
                        end={challenge.end}
                        difficulty={challenge.difficulty}
                        summits={challenge.summitsRef}
                        image={challenge.imageId}
                        logo={challenge.logoId}
                        map={challenge.mapId}
                        className={classNames(classes.challengeCard, classes.mt2, classes.mb2)}
                        onClick={() => this.joinChallenge(challenge)}
                    />
                </div>
            );
        });
    }


    render() {
        const { classes, translate } = this.props;
        const { posts } = this.props.doby;
        const summitsCount = this.state.challenge ? this.state.challenge.summitsRef.length : 0;
        let months = 0;
        let start = '';
        let end = '';
        let difficulty = 0;
        if (this.state.challenge) {
            months = moment(this.state.challenge.end).diff(moment(this.state.challenge.start), 'months', true);
            months = Math.abs(months);
            months = Math.ceil(months);
            start = moment(this.state.challenge.start).format('DD.MM');
            end = moment(this.state.challenge.end).format('DD.MM');
            difficulty = this.state.challenge.difficulty
        }
        return (
            <>
                <div className={classNames(classes.introSection)} style={{ backgroundImage: `url('${this.state.coverPhoto}')` }}>
                    <div className={classNames(classes.container, classes.h100, classes.introContent)}>
                        <div className={classNames(classes.dFlex, classes.flexCol, classes.h100, classes.w100)}>
                            <div className={classNames(classes.logoHolder, classes.w100, classes.textCenter, classes.mt10)}>
                                <img src={this.state.logo} className={classNames(classes.challengeLogo)} alt=""/>
                            </div>

                            <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt10)}>
                                <MountainIcon size={25} className={classes.textWhite} />
                                <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                                    {summitsCount} {summitsCount === 1 ? translate('challenge.summit') : summitsCount < 5 ? translate('challenge.summits') : translate('challenge.summit_gen')}
                                </div>
                            </div>
                            <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2)}>
                                <StopwatchIcon size={25} className={classes.textWhite} />
                                <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                                    {months} {months === 1 ? translate('challenge.month') : months < 5 ? translate('challenge.months') : translate('challenge.month_gen')}
                                </div>
                            </div>
                            <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2)}>
                                <FlagIcon size={25} className={classes.textWhite} />
                                <div className={classNames(classes.textH4, classes.fontMedium, classes.textWhite, classes.textMono, classes.ml3)}>
                                    {start} - {end}
                                </div>
                            </div>

                            <div className={classNames(classes.dFlex, classes.w100, classes.justifyContentBetween, classes.alignItemsCenter, classes.mt10, classes.mb11)}>
                                <div className={classNames(classes.dFlex, classes.flexCol)}>
                                    <div className={classNames(classes.textGray100, classes.textS, classes.textMono)}>
                                        {translate('challenge.difficulty')}
                                    </div>
                                    <div className={classNames(classes.dFlex, classes.alignItemsCenter, classes.mt2)}>
                                        <div className={classNames(classes.difficultyDot, difficulty >= 1 && classes.dotFilled)}></div>
                                        <div className={classNames(classes.difficultyDot, difficulty >= 2 && classes.dotFilled)}></div>
                                        <div className={classNames(classes.difficultyDot, difficulty >= 3 && classes.dotFilled)}></div>
                                        <div className={classNames(classes.difficultyDot, difficulty >= 4 && classes.dotFilled)}></div>
                                        <div className={classNames(classes.difficultyDot, difficulty >= 5 && classes.dotFilled)}></div>
                                    </div>
                                </div>

                                <img src={this.state.map} className={classNames(classes.challengeMap, classes.ml5)} alt=""/>
                            </div>


                        </div>
                    </div>
                </div>

                <div className={classNames(classes.bigMapSection, classes.dFlex, classes.justifyContentCenter)}>
                    <img src={this.state.bigMap} className={classNames(classes.bigMap)} alt=""/>
                </div>

                <div className={classNames(classes.bgSecondary, classes.mt4)}>
                    <div className={classNames(classes.container, classes.pb10)}>
                        <Grid container direction="row">
                            <Grid
                                item
                                xs={12}
                                className={classNames()}
                            >
                                <Button
                                    className={classNames(classes.acceptChallengeBtn)}                                     
                                    fullwidth
                                    arrow
                                >
                                    Prijat vyzvu od 22
                                </Button>
                            </Grid>
                            <Grid
                                item
                                xs={12}
                                className={classNames(classes.mt10)}
                            >
                                <h1
                                    className={classNames(
                                        classes.uppercase,
                                        classes.textWhite,
                                        classes.textLeft
                                    )}
                                >
                                    Zdol <br /> 6 ttov za  <br /> 3 mesiace?
                                </h1>
                                <div className={classNames(classes.textMono, classes.textGray100, classes.fontSemibold, classes.mt4)}>
                                    Ktor kopec pokor prv?
                                </div>
                            </Grid>
                        </Grid>

                        <Grid container direction="row" className={classNames(classes.mt9)}>
                            {this.renderSummits()}
                        </Grid>

                    </div>
                </div>

                <div className={classNames(classes.mt11, classes.mtMd13)}>
                    <Grid container direction="row">
                        <Grid item xs={12} md={6} className={classNames()}>
                            <div
                                className={classNames(
                                    classes.imageFitHolder,
                                    classes.bigPhotoHolder
                                )}
                            >
                                <img
                                    src={getSrc("p1.jpg")}
                                    className={classNames(classes.imageFit)}
                                    alt=""
                                />
                            </div>
                            <div
                                className={classNames(
                                    classes.dFlex,
                                    classes.justifyContentMdCenter,
                                    classes.mtMd7,
                                    classes.p5,
                                    classes.pMd0
                                )}
                            >
                                <div
                                    className={classNames(
                                        classes.dFlex,
                                        classes.flexCol,
                                        classes.bigPhotoContent
                                    )}
                                >
                                    <h4 className={classNames()}>
                                        {translate("landing.p1_title")}
                                    </h4>
                                    <div className={classNames(classes.mt3, classes.textBlock)}>
                                        {translate("landing.p1_description")}
                                    </div>
                                </div>
                            </div>
                        </Grid>

                        <Grid
                            item
                            xs={12}
                            md={6}
                            className={classNames(classes.mt5, classes.mtMd0)}
                        >
                            <div
                                className={classNames(
                                    classes.imageFitHolder,
                                    classes.bigPhotoHolder
                                )}
                            >
                                <img
                                    src={getSrc("p2.jpg")}
                                    className={classNames(classes.imageFit)}
                                    alt=""
                                />
                            </div>
                            <div
                                className={classNames(
                                    classes.dFlex,
                                    classes.justifyContentMdCenter,
                                    classes.mtMd7,
                                    classes.p5,
                                    classes.pMd0
                                )}
                            >
                                <div
                                    className={classNames(
                                        classes.dFlex,
                                        classes.flexCol,
                                        classes.bigPhotoContent
                                    )}
                                >
                                    <h4 className={classNames()}>
                                        {translate("landing.p2_title")}
                                    </h4>
                                    <div className={classNames(classes.mt3, classes.textBlock)}>
                                        {translate("landing.p2_description")}
                                    </div>
                                </div>
                            </div>
                        </Grid>
                    </Grid>
                </div>

                <div className={classNames(classes.bgSecondary)}>
                    <div className={classNames(classes.container, classes.py10)}>
                        <Grid container direction="row">
                            <Grid
                                item
                                xs={12}
                                className={classNames()}
                            >
                                <Button
                                    className={classNames()}                                     
                                    fullwidth
                                    arrow
                                >
                                    Prijat vyzvu od 22
                                </Button>
                            </Grid>  

                            <Grid
                                item
                                xs={12}
                                className={classNames(classes.mt7)}
                            >
                                <h3 className={classNames(classes.textWhite, classes.uppercase)}>
                                    alie vzvy
                                </h3>
                            </Grid>  

                            <Grid item xs={12} className={classNames()}>
                                <div className={classNames(classes.carouselFullwidth)}>
                                    <Flickity
                                        className={classNames(classes.mt4, classes.carouselSpacing)}
                                        options={flickityOptions}
                                        reloadOnUpdate={true}
                                    >
                                        {this.renderChallenges()}
                                    </Flickity>
                                </div>
                            </Grid>                         
                        </Grid>
                    </div>                
                </div>                

                <div
                    className={classNames(
                        classes.container,
                        classes.py10,
                        classes.pyMd11,
                        classes.pyLg12
                    )}
                >
                    <Grid container direction="row">
                        <Grid
                            item
                            xs={12}
                            md={5}
                            lg={4}
                            className={classNames(classes.mb5, classes.mbMd0)}
                        >
                            <h3 className={classNames(classes.uppercase)}>
                                {translate("landing.faq")}
                            </h3>
                        </Grid>
                        <Grid item xs={12} md={7} lg={8} className={classNames()}>
                            <Faq questions={posts.data} />
                        </Grid>
                    </Grid>
                </div>

                <div className={classNames(classes.findNewPeople)}>
                    <div className={classNames(classes.container, classes.h100)}>
                        <div
                            className={classNames(
                                classes.dFlex,
                                classes.flexCol,
                                classes.findNewPeopleContent,
                                classes.h100,
                                classes.justifyContentEnd,
                                classes.alignItemsStart
                            )}
                        >
                            <RedTriangle size={35} />
                            <h4
                                className={classNames(
                                    classes.textMono,
                                    classes.textWhite,
                                    classes.mb5,
                                    classes.mt2
                                )}
                            >
                                Spoznaj novch ud. <br />
                                14 000+ astnkov
                            </h4>
                        </div>
                    </div>
                </div>

                <div className={classNames(classes.footerBg)}>
                    <div className={classNames(classes.container)}>
                        <Grid container direction="row">
                            <Grid
                                item
                                xs={12}
                                className={classNames(
                                    classes.dFlex,
                                    classes.alignItemsCenter,
                                    classes.w100,
                                    classes.justifyContentBetween,
                                    classes.py6,
                                    classes.dMdNone
                                )}
                            >
                                <div className={classNames(classes.dFlex, classes.flexCol)}>
                                    <div className={classNames()}>
                                        {translate("contact.title")}
                                    </div>
                                    <a className={classNames(classes.fontBold, classes.noUnderline, classes.textSecondary)} href={`mailto:${translate('contact.email')}`}>
                                        {translate("contact.email")}
                                    </a>
                                </div>
                                <div
                                    className={classNames(
                                        classes.dFlex,
                                        classes.alignItemsCenter
                                    )}
                                >
                                    <a
                                        className={classNames(classes.mr5)}
                                        href={translate("contact.instagram_link")}
                                        rel="noreferrer"
                                    >
                                        <InstagramIcon size={25} />
                                    </a>
                                    <a
                                        className={classNames()}
                                        href={translate("contact.fb_link")}
                                        rel="noreferrer"
                                    >
                                        <FacebookIcon size={25} />
                                    </a>
                                </div>
                            </Grid>

                            <Grid
                                item
                                xs={12}
                                className={classNames(
                                    classes.alignItemsCenter,
                                    classes.w100,
                                    classes.py6,
                                    classes.dNone,
                                    classes.dMdFlex
                                )}
                            >
                                <div
                                    className={classNames(
                                        classes.dFlex,
                                        classes.alignItemsCenter
                                    )}
                                >
                                    <a
                                        className={classNames(classes.mr5)}
                                        href={translate("contact.instagram_link")}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <InstagramIcon size={25} />
                                    </a>
                                    <a
                                        className={classNames()}
                                        href={translate("contact.fb_link")}
                                        target="_blank"
                                        rel="noreferrer"
                                    >
                                        <FacebookIcon size={25} />
                                    </a>
                                </div>

                                <a className={classNames(classes.fontBold, classes.ml6, classes.noUnderline, classes.textSecondary)} href={`mailto:${translate('contact.email')}`}>
                                    {translate("contact.email")}
                                </a>
                            </Grid>
                        </Grid>
                    </div>
                </div>

            </>
        )
    }
}



export default compose(
    withStyles(styles),
    withUser,
    withDoby,
    withStore,
    withLocalization
)(ChallengeDetail);