import React from 'react';
import { withRouter } from 'react-router';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
//import LinearProgress from '@material-ui/core/LinearProgress';
import Grid from '@material-ui/core/Grid';
import getSharedStyles from '../../style';
import classNames from 'classnames';

import Select from '../shared/Select';
import ChallengeCard from '../dobyComponents/ChallengeCardV2'
//import ItemCard from './ItemCard';
import compose from '../../compose';
//import config from '../../config';
import withStore from '../../consumers/withStore';
import withDoby from '../../consumers/withDoby';
import withUser from '../../consumers/withUser';

import ProductSmallCard from '../dobyComponents/eshop/ProductSmallCard';
import Modal from "../dobyComponents/Modal";
import CloseIcon from '@material-ui/icons/Close';
import ProductDetail from './ProductDetail';

import Flickity from "react-flickity-component";
import { withLocalization } from '../shared/localization'

const styles = theme => ({
    ...getSharedStyles(theme),
    challengeCarousel: {
        '& .flickity-viewport': {
            //padding: '7px'
        },
        '& .carousel-cell:first-child': {
            paddingLeft: '1.25rem'
        },
        '& .carousel-cell:last-child': {
            paddingRight: '0.5rem',
        }
    },
    challengeList: {
        overflowX: 'auto',
        overflowY: 'hidden',
        position: 'relative',
        '&::-webkit-scrollbar': {
            display: 'none'
        }
    },
    challengeListWrapper: {
        display: 'flex',
    },
    itemFilterSelect: {
        marginBottom: '20px'
    },
    challengeCard: {
        width: '300px'
    },
    '@media screen and (max-width: 330px)': {
        productSmallCardHolder: {
            maxWidth: '100%',
            flexBasis: '100%'
        },
    },
    '@media screen and (min-width: 331px)': {
        productSmallCardHolder: {
            "&:nth-of-type(2n+1)": {
                paddingRight: "2px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "2px",
            },
        },
    },
    '@media screen and (min-width: 430px)': {
        productSmallCardHolder: {
            "&:nth-of-type(2n+1)": {
                paddingRight: "5px",
            },
            "&:nth-of-type(2n+2)": {
                paddingLeft: "5px",
            },
        },
    },
    productModalClose: {
        position: "fixed",
        top: "15px",
        right: "15px",
        cursor: "pointer",
        zIndex: "10",
        [theme.breakpoints.up("sm")]: {
            position: "absolute",
            top: "30px",
            right: "30px",
        },
    }
});

const flickityOptions = {
    cellAlign: 'left',
    contain: true,
    prevNextButtons: false,
    pageDots: false
}

class Eshop extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            itemFilterId: '1',
            productDetailModalOpen: false,
            productDetailCatalogId: null,
        };
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchProductsByCurrentFilter = async () => {
        const filter = [
            { field: "main", value: true },
        ];

        let sort = undefined;
        switch (this.state.itemFilterId) {
            //NOTE: most liked
            case '1': {
                sort = { field: "order_count", dir: "desc" };
                break;
            }
            //NOTE: cheapest
            case '2': {
                sort = { field: "price_with_tax", dir: "asc" };
                break;
            }
            //NOTE: most expensive
            case '3': {
                sort = { field: "price_with_tax", dir: "desc" };
                break;
            }
            default: {
                break;
            }
        }

        await this.props.doby.getProducts(filter, sort);
    }

    fetchData = async () => {
        await this.props.doby.getChallenges();
        await this.fetchProductsByCurrentFilter();
    }

    handleChange = (name, value) => {
        this.setState({ [name]: value });
    }

    handleChangeProductsFilter = async (name, value) => {
        this.setState({ [name]: value }, async () => await this.fetchProductsByCurrentFilter());
    }


    joinChallenge = (challenge) => {
        if (challenge._id)
            this.props.history.push(`/challenge-checkout-package/${challenge._id}`);
    }

    showProductDetail = (product) => {
        if (product.catalog_id.length > 0)
            this.props.history.push(`/eshop/product/${product.catalog_id}`);
    }

    renderChallenges = () => {
        const { challenges } = this.props.doby;
        const { classes } = this.props;
        return challenges.data.map(challenge => {
            //const endDate = new Date(challenge.end);
            return (
                <div className={"carousel-cell"} key={challenge._id}>
                    <ChallengeCard
                        title={challenge.name}
                        description={challenge.description}
                        start={challenge.start}
                        end={challenge.end}
                        difficulty={challenge.difficulty}
                        summits={challenge.summitsRef}
                        image={challenge.imageId}
                        logo={challenge.logoId}
                        map={challenge.mapId}
                        className={classNames(classes.challengeCard, classes.mt2, classes.mb2)}
                        onClick={() => this.joinChallenge(challenge)}
                    />
                </div>
            );
        });
    }

    handleCloseProductDetailModal = () => {
        this.setState({ productDetailModalOpen: false });
    }

    openProductModal = (catalogId) => {
        this.setState({ productDetailModalOpen: true, productDetailCatalogId: catalogId });
    }

    renderProducts = () => {
        const { products } = this.props.doby;
        const { classes } = this.props;

        let uniqueProducts = [];
        products.data.forEach(product => {
            let found = false;
            uniqueProducts.forEach(item => {
                if (product.catalog_id === item.catalog_id) {
                    found = true;
                    return;
                }
            })
            if (!found) uniqueProducts.push(product)
        })

        return uniqueProducts.map(product => {
            return (
                <Grid item xs={6} sm={3} lg={2} key={product._id} className={classNames(classes.productSmallCardHolder, classes.mb3)}>
                    {/* <ItemCard
                        onClick={()=> this.showProductDetail(product)}
                        name={product.name}
                        price={product.price_with_tax}
                        curr={config.currency}
                        Id={product.thumbnailId}
                        onViz={this.props.doby.getFile}
                        source={product.count > 0 ? this.props.translate('in_stock') : this.props.translate('out_stock')}
                        //membersOnly
                    /> */}
                    <ProductSmallCard
                        key={product._id}
                        title={product.name}
                        price={product.price_with_tax}
                        thumbnailId={product.thumbnailId}
                        catalogId={product.catalog_id}
                        onClickAddCart={() => console.log('yep')}
                        onClickViewDetails={() => this.showProductDetail(product)}
                        onClickAddCart={() => this.openProductModal(product.catalog_id)}
                    />
                </Grid>
            );
        })
    }

    render() {
        const { classes } = this.props;
        return (
            <div className={classNames(classes.bgLight)}>
                <div className={classNames(classes.bgSecondary)}>
                    <div className={classNames(classes.container, classes.pb9, classes.pt6, classes.pyMd0)}>
                        <Grid
                            container
                            direction="row"
                            justify="center"
                        >
                            <Grid item xs={12} className={classNames()}>
                                <h3 className={classNames(classes.textWhite, classes.uppercase, classes.mt4)}>
                                    Najbliie vzvy
                                 </h3>
                            </Grid>
                            <Grid item xs={12} className={classNames()}>
                                <div className={classNames(classes.carouselFullwidth)}>
                                    <Flickity
                                        className={classNames(classes.mt4, classes.carouselSpacing)}
                                        options={flickityOptions}
                                        reloadOnUpdate={true}
                                    >
                                        {this.renderChallenges()}
                                    </Flickity>
                                </div>
                            </Grid>
                        </Grid>
                    </div>
                </div>

                {/* <div className={classNames(classes.challengeList, classes.mt4)}>
                    <div className={classNames(classes.challengeListWrapper, classes.pt2, classes.pb2, classes.pl5)}>
                        { this.renderChallenges() }
                    </div>
                </div> */}

                <div className={classNames(classes.container, classes.mt8)}>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                    >
                        <Grid item xs={12} className={classNames()}>
                            <h3 className={classNames(classes.uppercase)}>
                                Vbava
                            </h3>
                        </Grid>
                        <Grid item xs={12} className={classNames()}>

                        </Grid>
                    </Grid>

                    <Select
                        className={classes.itemFilterSelect}
                        value={this.state.itemFilterId}
                        field="itemFilterId"
                        onChange={this.handleChangeProductsFilter}
                        data={[
                            { name: this.props.translate('most_liked'), _id: '1' },
                            { name: this.props.translate('cheapest'), _id: '2' },
                            { name: this.props.translate('most_expensive'), _id: '3' }
                        ]}
                        textField="name"
                        required
                    />
                    <Grid
                        container
                        direction="row"
                        justify="flex-start"
                        className={classNames(classes.productSmallCardContainer)}
                    >
                        {this.renderProducts()}
                    </Grid>
                </div>

                <Modal
                    isOpen={this.state.productDetailModalOpen}
                    wrapperClassName={classNames(
                        classes.p0,
                        classes.challengeModalWrapper
                    )}
                    onClose={this.handleCloseProductDetailModal}
                >
                    <div
                        className={classNames(classes.productModalClose)}
                        onClick={this.handleCloseProductDetailModal}
                    >
                        <CloseIcon size={32} />
                    </div>
                    <div className={classNames(classes.pt5)}>
                        <ProductDetail
                            {...this.props}
                            inModal
                            catalogId={this.state.productDetailCatalogId}

                        />
                    </div>
                </Modal>

            </div>
        );
    }
}

Eshop.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default compose(
    withRouter,
    withStore,
    withStyles(styles),
    withDoby,
    withUser,
    withLocalization
)(Eshop);