import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import getSharedStyles from '../../style';
import classNames from 'classnames';
import { withLocalization } from '../shared/localization'
import compose from '../../compose';
import Grid from "@material-ui/core/Grid";

import FacebookIcon from "../dobyComponents/icons/Facebook";
import InstagramIcon from "../dobyComponents/icons/Instagram";

const styles = theme => ({
   ...getSharedStyles(theme),
});

function Footer(props) { 
    const {classes, translate} = props;

       return (
           <>
           <div className={classNames(classes.bgSecondary, props.className)}>
                <div className={classNames(classes.container)}>
                    <Grid container direction="row">
                        <Grid
                            item
                            xs={12}
                            className={classNames(
                                classes.dFlex,
                                classes.alignItemsCenter,
                                classes.w100,
                                classes.justifyContentBetween,
                                classes.py6,
                                classes.dMdNone
                            )}
                        >
                            <div className={classNames(classes.dFlex, classes.flexCol)}>
                                <div className={classNames(classes.textGray100, classes.textMono, classes.mb1)}>
                                    {translate("contact.title")}
                                </div>
                                <a className={classNames(classes.fontBold, classes.noUnderline, classes.textWhite)} href={`mailto:${translate('contact.email')}`}>
                                    {translate("contact.email")}
                                </a>
                            </div>
                            <div
                                className={classNames(
                                    classes.dFlex,
                                    classes.alignItemsCenter
                                )}
                            >
                                <a
                                    className={classNames(classes.mr5, classes.dFlex, classes.alignItemsCenter)}
                                    href={translate("contact.instagram_link")}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <InstagramIcon size={25} className={classNames(classes.textWhite)}/>
                                </a>
                                <a
                                    className={classNames(classes.dFlex, classes.alignItemsCenter)}
                                    href={translate("contact.fb_link")}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <FacebookIcon size={25} className={classNames(classes.textSecondary)}/>
                                </a>
                            </div>
                        </Grid>

                        <Grid
                            item
                            xs={12}
                            className={classNames(
                                classes.alignItemsCenter,
                                classes.w100,
                                classes.py6,
                                classes.dNone,
                                classes.dMdFlex
                            )}
                        >
                            <div
                                className={classNames(
                                    classes.dFlex,
                                    classes.alignItemsCenter
                                )}
                            >
                                <a
                                    className={classNames(classes.mr5, classes.dFlex, classes.alignItemsCenter)}
                                    href={translate("contact.instagram_link")}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <InstagramIcon size={25} className={classNames(classes.textWhite)}/>
                                </a>
                                <a
                                    className={classNames(classes.dFlex, classes.alignItemsCenter)}
                                    href={translate("contact.fb_link")}
                                    target="_blank"
                                    rel="noreferrer"
                                >
                                    <FacebookIcon size={25} className={classNames(classes.textSecondary)}/>
                                </a>
                            </div>

                            <a className={classNames(classes.fontBold, classes.ml6, classes.noUnderline, classes.textWhite)} href={`mailto:${translate('contact.email')}`}>
                                {translate("contact.email")}
                            </a>
                        </Grid>
                    </Grid>
                </div>
            </div>
           </>
       )
}

export default compose(
   withStyles(styles),
   withLocalization
)(Footer);