import React from "react";
import PropTypes from "prop-types";
import Downshift from "downshift";
import { withStyles } from "@material-ui/core/styles";
import TextField from "@material-ui/core/TextField";
import Paper from "@material-ui/core/Paper";
import MenuItem from "@material-ui/core/MenuItem";

function renderInput(inputProps) {
  const { InputProps, classes, ref, ...other } = inputProps;

  return (
    <TextField
      InputProps={{
        inputRef: ref,
        classes: {
          root: classes.inputRoot,
          input: classes.inputInput,
        },
        ...InputProps,
      }}
      {...other}
    />
  );
}

function renderSuggestion({
  suggestion,
  index,
  itemProps,
  highlightedIndex,
  selectedItem,
  textField,
}) {
  const isHighlighted = highlightedIndex === index;
  const isSelected = selectedItem && suggestion._id === selectedItem._id;

  return (
    <MenuItem
      {...itemProps}
      key={suggestion._id}
      selected={isHighlighted}
      component="div"
      style={{
        fontWeight: isSelected ? 500 : 400,
      }}
    >
      {suggestion[textField]}
    </MenuItem>
  );
}
renderSuggestion.propTypes = {
  highlightedIndex: PropTypes.number,
  index: PropTypes.number,
  itemProps: PropTypes.object,
  selectedItem: PropTypes.string,
  suggestion: PropTypes.string.isRequired,
};

const styles = (theme) => ({
  root: {
    flexGrow: 1,
  },
  container: {
    flexGrow: 1,
    position: "relative",
  },
  paper: {
    position: "absolute",
    zIndex: 1,
    marginTop: theme.spacing.unit,
    left: 0,
    right: 0,
  },
  chip: {
    margin: `${theme.spacing.unit / 2}px ${theme.spacing.unit / 4}px`,
  },
  inputRoot: {
    flexWrap: "wrap",
  },
  inputInput: {
    width: "auto",
    flexGrow: 1,
  },
  divider: {
    height: theme.spacing.unit * 2,
  },
});

let _timer;
function IntegrationDownshift(props) {
  const { classes } = props;

  const onInputValueChange = (value) => {
    if (_timer) {
      clearTimeout(_timer);
    }

    _timer = setTimeout(() => {
      const filters = [{ field: props.textField, value, op: "regex" }];
      const sort = { field: props.textField, dir: "asc" };
      const page = { limit: 10, skip: 0 };
      props.onFilterChange(filters, sort, page);
    }, 500);
  };

  return (
    <div className={classes.root}>
      <Downshift
        itemToString={(item) => (item ? item[props.textField] : "")}
        id="downshift-simple"
        onChange={(value) => props.onChange(props.field, value)}
        onInputValueChange={onInputValueChange}
        selectedItem={props.value}
      >
        {({
          getInputProps,
          getItemProps,
          getMenuProps,
          highlightedIndex,
          inputValue,
          isOpen,
          selectedItem,
        }) => (
          <div className={classes.container}>
            {renderInput({
              fullWidth: props.fullWidth,
              classes,
              label: props.label,
              required: props.required,
              disabled: props.disabled,
              InputProps: getInputProps({
                placeholder: "",
              }),
            })}
            <div {...getMenuProps()}>
              {isOpen ? (
                <Paper className={classes.paper} square>
                  {props.data.map((suggestion, index) =>
                    renderSuggestion({
                      suggestion,
                      index,
                      itemProps: getItemProps({ item: suggestion }),
                      highlightedIndex,
                      selectedItem,
                      textField: props.textField,
                    })
                  )}
                </Paper>
              ) : null}
            </div>
          </div>
        )}
      </Downshift>
    </div>
  );
}

IntegrationDownshift.propTypes = {
  classes: PropTypes.object.isRequired,
  label: PropTypes.string.isRequired,
  onChange: PropTypes.func.isRequired,
  data: PropTypes.array.isRequired,
  value: PropTypes.object,
  required: PropTypes.bool,
  field: PropTypes.string.isRequired,
  textField: PropTypes.string.isRequired,
  onFilterChange: PropTypes.func.isRequired,
  disabled: PropTypes.bool,
  fullWidth: PropTypes.bool,
};

export default withStyles(styles)(IntegrationDownshift);
